import { request } from 'utils';

const prefix = '/api/categories';

export const getListBonusType = (page = 1, IsPagingEnabled = true) =>
  request({
    prefix,
    url: '/BonusType',
    params: {
      page,
      IsPagingEnabled,
      hasBTId: false,
    },
    method: 'GET',
  });
export const getBonusType = id =>
  request({
    prefix,
    url: `/BonusType/${id}`,
    method: 'GET',
  });
export const deleteBonusType = id =>
  request({
    prefix,
    url: `/BonusType/${id}`,
    method: 'DELETE',
  });
export const updateBonusType = (id, values) =>
  request({
    prefix,
    url: `/BonusType/${id}`,
    data: {
      id: values.id,
      label: values.code,
      btId: null,
    },
    method: 'PUT',
  });

export const createBonusType = values =>
  request({
    prefix,
    url: '/BonusType',
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      typeId: values.typeId,
    },
    method: 'POST',
  });



